@import "../variables.scss";
@import "../mixins/media";

.header {
  position: relative;
}

.header-nav {
  $header-nav: & !global;
  display: flex;
  flex-direction: row;
  justify-content: center;
  flex-wrap: wrap;
  max-width: 100%;
  align-items: center;
  border-bottom: 1px solid $border-bottom-nav-color;


  @include media-tablet {
    display: none;
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    flex-direction: column;
    justify-content: flex-start;
    flex-wrap: nowrap;
    margin: 0;
    padding: 100px 0 0;
    background: $basic-white;
    overflow: auto;
  }

  .no-js & {
    @include media-tablet {
      display: flex;
      position: relative;
      margin: 10px 0;
      padding: 0;
    }
  }

  a {
    font-size: 16px;
    font-weight: 300;
    line-height: 32px;
    text-align: center;
    letter-spacing: -0.005em;
    white-space: nowrap;
    text-decoration: none;
    margin: 10px 48px 0;
    padding: 10px 20px;
    color: $basic-white;
    opacity: 0.7;

    &:hover {
      color: $accent-color;
    }

    &:focus {
      outline: 2px solid;
      opacity: 1;
      color: $accent-color;
    }

    &:first-child {
      margin-left: 0;
    }

    &:last-child {
      margin-right: 0;
    }

    &:nth-child(4n) {
      margin-right: 0;
    }

    &:nth-child(5n) {
      margin-left: 0;
    }

    @include media-tablet {
      align-self: stretch;
      margin: 0 0 32px;
      padding: 15px;
      font-weight: normal;
      font-size: 20px;
      line-height: 28px;
      text-align: start;
      color: $header-nav-link-color;
      background: $header-nav-link-background-color url("data:image/svg+xml,%3Csvg width='8' height='12' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cg clip-path='url(%23clip0)'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M0 1.4L4.6 6 0 10.6 1.4 12l6-6-6-6L0 1.4z' fill='%232C2E3F' fill-opacity='.5'/%3E%3C/g%3E%3Cdefs%3E%3CclipPath id='clip0'%3E%3Cpath fill='%23fff' transform='rotate(-90 6 6)' d='M0 0h12v7.4H0z'/%3E%3C/clipPath%3E%3C/defs%3E%3C/svg%3E") 94% 50% no-repeat;
      opacity: 1;
    }
  }
}

.header-nav--active {
  display: flex;
}

.header-toggle {
  display: none;

  @include media-tablet {
    display: block;
    width: 50px;
    height: 50px;
    margin: 0 0 0 auto;
    padding: 0;
    flex-shrink: 0;
    flex-grow: 0;
    box-sizing: border-box;
    background: $accent-color url("data:image/svg+xml,%3Csvg width='22' height='14' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M.75 14h22.5v-1H.75v1zm0-6.25h22.5v-1H.75v1zM.75.5v1h22.5v-1H.75z' fill='%23000'/%3E%3C/svg%3E") 50% 50%/30px auto no-repeat;
    border: none;

    .no-js & {
      display: none;
    }
  }
}

.header-toggle--active {
  position: relative;
  z-index: 3;
  background: url("data:image/svg+xml,%3Csvg width='24' height='25' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1 23.721L23 1m0 22.721L1 1' stroke='%23141517' stroke-opacity='.2' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E") 50% 50%/20px auto no-repeat;
}
